.ContactData {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}

.ContactData .Button {
  font-size: 17px;
  border-radius: 3px;
  padding: 6px;
  margin-top: 7px;
}


@media (min-width: 600px) {
    .ContactData {
        width: 500px;
    }
}